<template>
   <div class="buyer">
      <div class='mine_top'>
         <div class='return' @click='retreat()'></div>
         <span>创建买单</span>
      </div>
      <div class='setmenu'>
         <div class='currency'>
            <p>购买币种</p>
            <select v-model="takex">
               <option v-for='(item,index) in takes' :key='index'>{{item}}</option>
            </select>
         </div>
         <van-cell-group>
            <van-field
            label="购买数量"
            v-model="sale_num"
            placeholder="请输入您要购买token币数量" />
         </van-cell-group>
         <div class='currency'>
            <p>支付币种</p>
            <select v-model="take">
               <option v-for='(item,index) in takes' :key='index'>{{item}}</option>
            </select>
         </div>
         <van-cell-group>
            <van-field
            label="单价"
            v-model="price"
            placeholder="请输入货币的单价" />
         </van-cell-group>
         <van-cell-group>
            <van-field
            label="支付数量"
            disabled
            :placeholder="sale_num * price == 0 ? '请输入支付数量' : sale_num * price" />
         </van-cell-group>
         <van-cell-group>
            <van-field
            label="手机号"
            disabled
            :placeholder="phone" />
         </van-cell-group>
            <div class='site'></div>
            <div class='getsite'>
               <p  style='margin-bottom:50px;'>
                  <van-icon @click='err_btn()' class='err' name="cross" />
                  请输入支付密码
               </p>
               <!-- 密码输入框 -->
               <van-cell-group>
                  <van-field v-model="value" type='password' placeholder="请输入您的交易密码" />
               </van-cell-group>
               <!-- 确认键 -->
               <van-button class='btn' @click='Submit()' size="large">确认密码</van-button>
            </div>        
      </div>
      <van-button class='btn' @click='sale()' size="large">确认支付</van-button>
   </div>
</template>

<script>

import axios from 'axios'

export default {
   name: '',
   data() {
       return {
          phone:localStorage.phone,
          price:'',           // 单价
          takex:'',       // 购买币种
          take:'',       // 支付币种
          takes:[],       // 币种种类
          sale_num:'',        // 购买数量
          showKeyboard: true,
          value:''  // 支付密码
       }
   },
   created(){
      this.Currency();
   },
   watch:{
      price(New,Old){
         if( !/^[0-9]*$/.test(New) ){
            this.$dialog.alert({
               message: '请输入正确价格'
            })
            this.price = '';
         }
      },
      sale_num(New,Old){
         if( !/^[0-9]*$/.test(New) ){
            this.$dialog.alert({
               message: '请输入正确价格'
            })
            this.sale_num = '';
         }
      }
   },
   methods:{
      retreat(){
         this.$router.go(-1);
      },
      Submit(){
            let url = window.globalData.config.domain + '/otc/buyPendOrder';
            axios.post(url,{
               cmd:'201',
               body:{
                  "accessToken":localStorage.token,
                  "from_token":this.take,  // 购买的币种
                  "from_amount":this.pay, // 购买的数量
                  "dst_token":this.takex,        // 支付的币种
                  "dst_token_amount":this.sale_num,  // 支付的数量
                  "price":this.price,           // 单价     
                  "phone":this.phone,           // 手机号码
                  "paypasswd":this.value,       // 支付密码
                  "notice":""
               }
            }).then( (res) => {
               // console.log(res);
               if( res.data.code === 217 ){
                  this.$dialog.alert({
                     message: '密码输入错误'
                  })
                  this.value = '';
               }else if( res.data.code === 116 ){
                  this.$dialog.alert({
                     message: '您的余额不足'
                  })
                  this.value = '';
               }else if( res.data.code === 132 ){
                  this.$dialog.alert({
                     message: '请绑定支付宝、微信或者银行卡'
                  })
                  this.$router.go(-1);
               }else if( res.data.code === 133 ){
                  this.$dialog.alert({
                     message: '请绑定'+this.take+'地址'
                  })
                  this.$router.go(-1);
               }else if( res.data.code === 0 ){
                  this.$dialog.alert({
                     message: '订单创建成功'
                  })
                  this.$router.go(-1);
                  // console.log('1')
               }else{
                  this.$dialog.alert({
                     message: '订单创建失败'
                  })
                  this.$router.go(-1);
               }
            } )
      },
      setsite(){
         let site = document.getElementsByClassName('site')[0];
         let getsite = document.getElementsByClassName('getsite')[0];
         site.style.display = 'block';
         getsite.style.display = 'block';
      },
      err_btn(){
         let site = document.getElementsByClassName('site')[0];
         let getsite = document.getElementsByClassName('getsite')[0];
         site.style.display = 'none';
         getsite.style.display = 'none';
      },
      sale(){ 
         if( this.takex === '' ){
            this.$dialog.alert({
               message: '请输入您要购买的货币种类'
            })
         }else if( this.sale_num === '' || this.sale_num == '0' ){
            this.$dialog.alert({
               message: '请输入您要购买的货币数量'
            })
         }else if( this.take === '' ){
            this.$dialog.alert({
               message: '请选择您要支付的货币种类'
            })
         }else if( this.price === '' || this.price == '0' ){
            this.$dialog.alert({
               message: '请输入您要购买的货币单价'
            })
         }else{
            this.setsite();
         }
      },
      Currency(){
         let url = '/blockchain/getSupportCoins';
         this.$http.post(url,{
            cmd:'',
            body:{}
         }).then( (res) => {
            this.takes = res;
            // console.log(this.take)
         })
      }
   },
}
</script>

